<template>
	<view class="content" >
		<view style="padding: 24rpx 24rpx 0;" @tap.stop="navigatePostReplay(item.post.post_id)">
			<view class="itemDetailTop">
				<view class="topLeft">
					<view class="userImage">
						<image :src="item.user.avatar_url|imageUrlReset(50,80)" mode="aspectFill" class="userAvatarImage" :lazy-load="true"></image>
						<image :src="item.user.pendant|imageUrlReset(50,80)" :lazy-load="true" mode="aspectFill" class="userAvatarFrameImage" v-if="item.user.pendant"></image>
						<image v-if="item.user.certification && item.user.certification.type"
							:src="`../../static/poseRequlay/certificate${item.user.certification.type}.png`" mode="widthFix" class="certificate">
						</image>
					</view>
					<view class="userMessage">
						<view class="userName">
							{{item.user.nickname}}
							
							<image :src="`https://bbs-static.miyoushe.com/level/level${item.user.level_exp.level}.png`"
								mode="heightFix" class="levelLogo">
							</image>
						</view>
					</view>
				</view>
				<view class="topRight">
					{{item.post.created_at|changeMonAndDay}}{{item.post.game_id|changeGameType}}
				</view>
			</view>
			<view class="itemDetailContent">
				<view class="itemDetailTextTitle" v-if="item.post.subject" >
					<view class="itemDetailTextTitleDetail" v-html="matchText(item.post.subject,1)"></view>
				</view>
				<view v-if="item.post.content" >
					<view class="itemDetailTextContent" v-html="matchText(item.post.content,2)"></view>
				</view>
				<view class="itemDetailImage">
					<view v-if="item.image_list.length > 0 && !item.vod_list.length" class="imageList" :class="'imageList'+ item.image_list[0].entity_id">
						<view v-if="item.image_list.length >= 2" class="image2">
							<image :src="item.image_list[0].url|imageUrlReset(300,80)" mode="aspectFill" class="showImage" :lazy-load="true" @tap.stop="showImage(0)"></image>
							<image :src="item.image_list[1].url|imageUrlReset(300,80)" mode="aspectFill" class="showImage" :lazy-load="true" @tap.stop="showImage(1)"></image>
						</view>
						<view v-if="item.image_list.length < 2" class="image1">
							<image :src="item.image_list[0].url|imageUrlReset(300,80)" mode="aspectFill" class="showImage" :lazy-load="true" @tap.stop="showImage(0)"></image>
						</view>
						<view class="moreImage" v-if="item.image_list.length > 2">
							{{item.image_list.length}}
						</view>
					</view>
					<view v-if="item.vod_list.length > 0" class="vodieList" :style="{'width': videoWidth, 'height': videoHeight+'rpx'}">
						<video :src="videoUrl"  :id="'video'+ item.vod_list[0].id" v-if="videoUrl"
							:autoplay="false" :muted="!videoMute" :ref="'video'+ item.vod_list[0].id"
							object-fit="cover" :show-center-play-btn="false"  :show-fullscreen-btn="false"
							:poster="item.vod_list[0].cover" :controls="false"  :show-mute-btn="false"
							:style="{'width': videoWidth, 'height': videoHeight +'rpx'}" :class="'videoDetail'+item.vod_list[0].id" 
							@ended = "videoEnded"  @timeupdate="timeupdate"
							>
						</video>
						<view class="videoButton" v-if="!is_stop">
							<view class="muteBtn" @tap.stop="controlVideoMute">
								<u-icon name="volume" color="#ffffff" size="28" v-if="videoMute"></u-icon>
								<u-icon name="volume-off" color="#ffffff" size="28"  v-if="!videoMute"></u-icon>
							</view>
							<view class="progress">
								<view class="progressLine" :style="{'width': viddeoPercentage + '%'}"></view>
							</view>
						</view>
						
						<view class="videoStep">
							{{videoStep}}
						</view>
						
						 <view class="videoWrapp" v-if="is_stop" @tap="videoStart(item.vod_list[0].id)"></view>
						 
						 <view class="videoEndedWrap" v-if="videoEnd">
						 	<view class="videoEndedWraper">
						 		<view class="userImage" @tap.stop="entryUserHome(item.user.uid)">
						 			<image :src="item.user.avatar_url|imageUrlReset(50,80)" mode="aspectFill" class="userAvatarImage" :lazy-load="true"></image>
						 			<image :src="item.user.pendant|imageUrlReset(50,80)" :lazy-load="true" mode="aspectFill" class="userAvatarFrameImage" v-if="item.user.pendant"></image>
						 		</view>
						 		<view class="userName">
						 			{{item.user.nickname}}
						 		</view>
						 		<view class="follow" @tap.stop="followUser(item.user.uid)">
						 			+ 关注
						 		</view>
						 		<view class="replayVideo" @tap.stop="replayVideo">
						 			<image src="../../static/replay.png" mode="aspectFill"></image>
						 			重播
						 		</view>
						 	</view>
						 </view>
					</view>
				</view>
			</view>
			<view class="itemDetailBottom">
				<view class="topicsName" v-if=" item.topics.length > 0">{{item.topics[0].name}}</view>
				<view class="itemDetail">
					<u-icon name="thumb-up" color="#ddd" size="46"></u-icon>
					{{item.stat.like_num}}
				</view>
				<view class="itemDetail">
					<u-icon name="chat" color="#ddd" size="46"></u-icon>
					{{item.stat.reply_num}}
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	import {mapState,mapMutations} from 'vuex'
	export default {
		name:"postItems",
		props: {
			item: {
				type: Object,
				default() {
					return {}
				}
			},
			keyWord: {
				type: String,
				default() {
					return ''
				}
			}
		},
		data() {
			return {
				is_stop: true,
				videoStep: 0,
				viddeoPercentage: 0,
				videoWidth: '100%',
				videoHeight: 500,
				videoId: null,
				videoEnd: false,
				intervalTimer: null,
				imageId: null,
				intersectionObserverVideo: null,
				videoUrl: ''
			};
		},
		computed: {
			...mapState(['videoMute']),
		},
		created() {
			uni.getSystemInfo({
				success: res => {
					this.windowHeight = res.windowHeight
				}
			})
			this.resetWH()
			this.resetDurationTime()
		},
		methods: {
			...mapMutations(['updateVideoMute']),
			videoStart(type) {
				this.$nextTick(function () {
					const ref = 'video' + this.videoId
					this.is_stop = false
					let videoContext
					// #ifdef MP-WEIXIN
					videoContext = uni.createVideoContext(ref,this)
					// #endif
					// #ifndef MP-WEIXIN
					videoContext = this.$refs[ref]
					// #endif
					if(type === 'replay') {
						this.videoEnd = false
						videoContext.pause()
						videoContext.currentTime = 0	
						videoContext.play()
					}else{
						this.videoEnd = false
						videoContext.play()
					}
				})
			},
			videoStop() {
				const id = this.videoId
				this.$nextTick(function () {
					if(!this.is_stop) {
						const ref = 'video' + this.videoId
						this.is_stop = true
						// #ifdef MP-WEIXIN
						const videoContext = uni.createVideoContext(ref,this)
						videoContext.pause();
						// #endif
						// #ifndef MP-WEIXIN
						this.$refs[ref].pause()
						// #endif
					}

				})

			},
			resetWH() {
				if(this.item.vod_list.length) {
					const video = this.item.vod_list[0]
					let resolutions
					if(video.resolutions.length > 1) {
						resolutions = video.resolutions[video.resolutions.length - 1]
					}else{
						resolutions = video.resolutions[0]
					}
					this.videoDetail = video
					this.videoUrl = resolutions.url
					const width = resolutions.width
					const height = resolutions.height
					const per = width / height
					if(per > 1) {
						this.videoHeight = 702 * height / width
					}else{
						this.videoWidth = '400rpx'
						this.videoHeight = 400 * height / width
					}
					const id = video.id
					this.videoId = id
				}else if(this.item.image_list.length){
					this.imageId = this.item.image_list[0].entity_id
				}
			},
			resetDurationTime() {
				if(this.item.vod_list.length) {
					const time = Number(this.item.vod_list[0].duration)
					this.videoStep = this.getStep(time)
				}
			},
			getStep(time) {
				if(time >= 60000) {
					const M = parseInt(time / 60000) >= 10 ? parseInt(time / 60000): '0'+ parseInt(time / 60000)
					const S = parseInt(time % 60000 / 1000) >= 10 ? parseInt(time % 60000 / 1000): '0' + parseInt(time % 60000 / 1000)
					return `${M}:${S}`
				}else{
					const S = parseInt(time / 1000) >= 10 ? parseInt(time / 1000): '0' + parseInt(time / 1000)
					return  `00:${S}`
				}
			},
			controlVideoMute() {
				const oldVideoMute = this.videoMute
				this.updateVideoMute(!oldVideoMute)
			},
			videoEnded() {
				this.videoEnd = true
			},
			againVideoPlay() {
				this.videoEnd = false
				this.videoStart()
			},
			timeupdate(e) {
				const currentTime = e.detail.currentTime
				const durationTime = e.detail.duration
				this.currentTime = currentTime
				this.durationTime = durationTime
				this.viddeoPercentage = (currentTime + 1) / durationTime * 100
				const num = durationTime - currentTime
				const m = parseInt(num / 60) >=10 ? parseInt(num / 60) : '0' + parseInt(num / 60)
				const s = parseInt(num % 60) >=10 ? parseInt(num % 60) : '0' + parseInt(num % 60)
				this.videoStep = `${m}:${s}`
			},
			replayVideo() {
				this.videoStart('replay')
			},
			followUser(userId) {
				console.log(userId)
			},
			entryVideoDetail() {
				
			},
			entryUserHome(userId) {
				console.log(userId)
			},
			showImage(index) {
				uni.$emit('openImgPopUp',this.item.image_list,index)
			},
			navigatePostReplay(post_id) {
				if(this.videoUrl) {
					uni.navigateTo({
						url: `/subPackages/postReplay/postReplay?post_id=${post_id}`,
					})
				}else{
					uni.navigateTo({
						url: `/subPackages/artical/artical?post_id=${post_id}`,
					})
				}
			},
			matchText(text,type) {
				const reg = new RegExp(`${this.keyWord}`,'g')
				if(this.item.user.certification && this.item.user.certification.type === 1 && type === 1) {
					text = `<img class="logoImage" src="../../static/search/certificate01.png" style="width: 20px;height: 20px;font-size: 0;vertical-align: bottom;"/>${text}`
				}
				if(type === 2) {
					text = `${text}...`
				}
				
				return text.replace(reg, `<span style="color: #71e0fe">${this.keyWord}</span>`)
			}
		},
		filters: {
			changeMonAndDay(value) {
				const date = new Date(parseInt(value) * 1000)
				const createDay = date.getDate() < 10 ? `0${date.getDate()}` : date.getDate()
				const createMon = date.getMonth() + 1 < 10 ? `0${date.getMonth()}` : date.getMonth()+1
				const year = date.getFullYear()
				return `${year}-${createMon}-${createDay}`
			},
			changeGameType(value) {
				if(value === 2) {
					return ` · 原神`
				}
			}
		},
		mounted() {
			this.$nextTick(function () {
				if(this.videoId) {
					this.intersectionObserverVideo = uni.createIntersectionObserver(this);
					const top = this.windowHeight / 2 - 30
					this.intersectionObserverVideo.relativeToViewport({bottom:-300,top:-top,left:0,right: 0}).observe(`.videoDetail${this.videoId}`, (res) => {
					  if (res.intersectionRatio > 0) {
						this.videoStart()
					  } else{
						this.videoStop()
					  }
					});
				}
			})

		}
	}
</script>

<style lang="scss" scoped>
	.content{
		border-bottom: 6px solid #f8f8f8;
	}
.itemDetailTop{
	display: flex;
	justify-content: space-between;
	
	.topLeft{
		display: flex;
		align-items: center;
		height: 48rpx;
		padding: 12rpx;
		
		.userImage{
			width: 48rpx;
			height: 48rpx;
			position: relative;
			
			.userAvatarImage{
				width: 48rpx;
				height: 48rpx;
				border-radius: 48rpx;
			}
			
			.userAvatarFrameImage{
				width: 60rpx;
				height: 60rpx;
				position: absolute;
				left: -6rpx;
				top: -6rpx;
			}
			
			.certificate{
				position: absolute;
				right: -2rpx;
				bottom: -2rpx;
				width: 16rpx;
				height: 16rpx;
			}
		}
		
		.userMessage{
			padding-left: 12rpx;
			
			.userName{
				font-size: 24rpx;
				color: #000;
				display: flex;
				align-items: center;
				
				.levelLogo{
					height: 24rpx;
					margin-left: 5px;
				}
			}
			
			.userLabel{
				font-size: 22rpx;
				color: #ddd;
			}
		}
	}
	
	.topRight{
		padding-top: 24rpx;
		box-sizing: border-box;
		color: #ddd;
		font-size: 24rpx;
	}
}
.itemDetailContent{
	.itemDetailTextTitle{
		font-size: 28rpx;
		line-height: 1.5;
		padding: 8rpx 0;
		color: #000;
		
		.itemDetailTextTitleLogo{
			display: inline-block;
			width: 40rpx;
			height: 40rpx;
			margin-right: 2px;
			vertical-align: bottom;
			
			.logoImage{
				width: 40rpx;
				height: 40rpx;
				font-size: 0;
			}
		}
		
		.itemDetailTextTitleDetail{
			display: inline;
		}
	}
	
	.itemDetailTextContent{
		font-size: 26rpx;
		line-height: 1.5;
		color: #ddd;
		display: block;
		margin-bottom: 6px;
		display: -webkit-box;
		overflow: hidden;
		text-overflow: ellipsis;
		-webkit-box-orient: vertical;
		-webkit-line-clamp: 4;
	}
	
	.itemDetailImage{
		position: relative;
		
		.imageList{
			font-size: 0;
			max-height: 460px;
			overflow: hidden;
			
			.image2{
				max-height: 346rpx;
				display: flex;
				justify-content: space-between;
				
				.showImage{
					width: 346rpx;
					height: 346rpx;
					border-radius: 8px;
					padding: 0 2rpx;
					box-sizing: border-box;
				}
			}
			.image1{
				position: relative;
				
				.showImage{
					display: inline-block;
					width: 460rpx;
				}
			}
			

			
			.moreImage{
				position: absolute;
				padding: 2px 4px 2px 20px;
				background-image: url("../../static/imgCopy.png");
				background-color: rgba(0, 0, 0, 0.5);
				background-size: 12px auto;
				background-position: 4px center;
				background-repeat: no-repeat;
				bottom: 6px;
				right: 6px;
				color: #fff;
				font-size: 20rpx;
				border-radius: 4px;
			}
		}
		
		.vodieList{
			border-radius: 4px;
			overflow: hidden;
			position: relative;
			
			.videoWrapp{
				width: 100%;
				height: 100%;
				position: absolute;
				background-color: rgba(0, 0, 0, 0.5);
				top: 0;
				left: 0;
				background-image: url("../../static/videoIcon.png");
				background-size: 72rpx 72rpx;
				background-position: center center;
				background-repeat: no-repeat;
			}
			
			.videoButton{
				display: flex;
				justify-content: space-between;
				position: absolute;
				bottom: 0;
				left: 0;
				height: 30px;
				width: 100%;
				align-items: center;
				box-sizing: border-box;
				
				.muteBtn{
					width: 30px;
					height: 30px;
					display: flex;
					align-items: center;
					justify-content: center;
				}
				
				.progress{
					bottom: 0;
					left: 0;
					position: absolute;
					width: 100%;
					height: 3px;
					background: rgba(255,255,255,0.5);
					
					.progressLine{
						background: #3c9cff;
						transition-duration: 500ms;
						height: 3px;
						border-radius: 3px;
					}
				}
			}
			
			.videoStep{
				position: absolute;
				right: 8rpx;
				bottom: 8rpx;
				font-size: 20rpx;
				color: #fff;
				padding:2px 5px;
				background: rgba(0, 0, 0, 0.5);
				border-radius: 4px;
			}
			
			video{
				width: 100%;
			}
			
			.videoEndedWrap{
				position: absolute;
				left: 0;
				top:0;
				text-align: center;
				width: 100%;
				height: 100%;
				background: #000;
				
				.videoEndedWraper{
					position: absolute;
					left: 50%;
					top:50%;
					transform: translate(-50%,-50%);
					width:70%;
				}
				
				.userImage{
					width: 72rpx;
					height: 72rpx;
					position: relative;
					margin: 0 auto;
					
					.userAvatarImage{
						width: 72rpx;
						height: 72rpx;
						border-radius: 72rpx;
					}
					
					.userAvatarFrameImage{
						width: 84rpx;
						height: 84rpx;
						position: absolute;
						left: -6rpx;
						top: -6rpx;
					}
				}
				
				.userName{
					color: #fff;
					font-size: 24rpx;
					padding-bottom: 4px;
					margin-top: 6px;
				}
				
				.follow{
					height: 24px;
					line-height: 24px;
					width: 70px;
					border-radius: 4px;
					background: #3c9cff;
					color: #fff;
					text-align: center;
					font-size: 20rpx;
					margin: 0 auto;
					margin-bottom: 4px;
				}
				.replayVideo{
					display: flex;
					justify-content: center;
					color: #ddd;
					font-size: 20rpx;
					align-items: center;
					width: 70px;
					margin: 0 auto;
					
					image{
						width: 12px;
						height: 12px;
						margin-right: 3px;
					}
				}
			}
		}
	
	}

}

.itemDetailBottom{
	display: flex;
	justify-content: flex-end;
	align-items: center;
	position: relative;
	font-size: 22rpx;
	color: #999;
	height: 48rpx;
	padding: 20rpx 0 24rpx;
	
	.topicsName{
		position: absolute;
		left: 0;
		display: block;
		padding:2px 8px ;
		border-radius: 2px;
		background: #f7f7f7;
	}
	
	.itemDetail{
		display: flex;
		align-items: center;
		padding-left: 24rpx;
	}
}
</style>